<template>
	<div class="authority_wrap" data-commont="新建素材">
		<el-row class="back">
			<el-col :span="0.8">
				<a href="javascript:history.back(-1)">
					<span class="el-icon-arrow-left back_span">返回</span>
				</a>
			</el-col>
			<el-col :span="0.4" style="color: #ccc;">|</el-col>
			<el-col :span="2">
				<span>人员绑定</span>
			</el-col>
		</el-row>
		
   <!--table-->
		<el-table :data="roleData" style="width: 100%":show-header=false >
		    <el-table-column type="expand">
		      	<template slot-scope="scope">
		        	<div  style="height: 150px; overflow: auto;">
		        		<el-table :data="peopleData">
					        <el-table-column prop="orgName" label="部门名称">
					        </el-table-column>
					        <el-table-column  prop="orgCode"label="部门编码" >
					        </el-table-column>
					        <el-table-column prop="loginName" label="登录账号">	
					        </el-table-column>
					        <el-table-column label="密码重置" width="260" >
					      		<template slot-scope="scope">
					      			<el-button @click="add">密码重置</el-button>
					      		</template>
					        </el-table-column>
					    </el-table>
		        	</div>
		
	      		</template>
		    </el-table-column>
			
		    <el-table-column>
		    	<template slot-scope="scope">
		    		<span>{{scope.row.name}}</span>
		    	</template>
		    </el-table-column>	
		     <el-table-column>
		    	<template slot-scope="scope">
		    		<span style="float: right;" >
		    			<el-button @click="boundMemberDiadig = true">绑定成员</el-button>
		    		</span>
		    	</template>
		    </el-table-column>		   
		</el-table>
		
		<!--绑定成员弹出框-->
		<el-dialog
		    title="选择绑定成员"
		    :visible.sync="boundMemberDiadig"
		    width="60%"
		    :before-close="handleClose">
			<el-row :gutter="20">
			 	<el-col :span="8">
			 		<h3>选择:</h3>
			 		<el-input placeholder="搜索部门"  v-model="filterText">
					</el-input>
					<el-tree
					  class="filter-tree"
					  :data="cascaderData"
					  :filter-node-method="filterNode"
					  ref="tree">
					</el-tree>
			 	</el-col>
			 	<el-col :span="14">
			 		<h3>已选</h3>
			 		<el-table
					    ref="multipleTable"
					    :data="peopleData"
					    style="width: 100%">
					    <el-table-column type="selection" width="55">
					    </el-table-column>
					    <el-table-column  label="岗位">
					        <template slot-scope="scope">{{ scope.row.post}}</template>
					    </el-table-column>
					    <el-table-column  prop="name" label="姓名">
					    </el-table-column>
					</el-table>
			 	</el-col>
			</el-row>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="boundMemberDiadig = false">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import cascader from './cascader'
	import tableData from './table'
	import roleData from './roleData' //角色
	import peopleData from './peopleData' //绑定人
		export default {
	    name: 'authority',
	    
	    data() {
	      	return {
		        createDialogVisible: false,
				cascaderData:cascader,
				tableData:tableData,
				roleData:roleData,
				peopleData:peopleData,
				boundMemberDiadig:false,  //绑定成员弹出框
				filterText:'', //绑定选择input值
				multipleSelection: [], //多选表格
				
				//创建者弹出框
				ruleForm:{
					name:''
				},
				rules: {
				 	name: [
			            { required: true, message: '请输入创建者姓名', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
			       
				}
	      	}
	    },
	    watch: {
	        filterText(val) {
	        	this.$refs.tree.filter(val);
	        }
	    },
	    computed: {
	    	
	    },
	    methods:{
	    	//添加角色确定
	    	addTreeFun(formName){

	    		this.$refs[formName].validate((valid) => {
					if (valid) {
	    				this.createDialogVisible = false
	    				this.roleData.push({
	    					name:this.ruleForm.name
	    				})
	    				this.$router.push({
	    					name:'binder_list'
	    				})
					} else {
						console.log('error submit!!');
						return false;
					}
				});
	    	
	    	},
	    	//绑定选择人员tree筛选
	    	filterNode(value, data) {
        		if (!value) return true;
        		return data.label.indexOf(value) !== -1;
      		},
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		          .then(() => {
		            done();
		          })
		          .catch(() => {});
		      	}
			}
  	}

</script>

<style lang="scss" slot-scope="scope">
	$border:1px solid #eee;
	.authority_wrap{
		background: #fff;
		.back{	
			height: 50px;
			line-height: 50px;
			border-bottom: $border;
			font-size:1.6rem;
			background:#66B1FF;
			color:#f5f5f5;
			div{
				margin-left: 10px;
				
			}
		}
	}
	
</style>


